import NProgress from 'nprogress'
import router from './router'
import store from '@/store/store'

// 
import 'nprogress/nprogress.css'
import { getToken } from '@/permission/token'

NProgress.configure({ showSpinner: false }) // NProgress 

const whiteList = ['/sign']

router.beforeEach(async (to, from, next) => {
  NProgress.start() // 

  // 
  const title = to.meta?.title
  document.title = title ? `${title} - ` : ''

  const hasToken = !!getToken()

  if (hasToken) {
    const isLocked = JSON.parse(localStorage.getItem('screen_lock'))?.isLocked
    if (isLocked && to.path !== '/lock-screen') {
      next({ path: '/lock-screen' })
      NProgress.done()
    } else if (to.path === '/sign') {
      //  token 
      next({ path: '/' })
    } else {
      const hasPermissions = !!(store.state.admin.info?.permissions?.length > 0)

      if (hasPermissions) {
        next()
      } else {
        const { permissions } = await store.dispatch('admin/getAdminInfo')
          .catch(() => {
            next('/sign')
          })
        const accessedRoutes = await store.dispatch('permission/generateRoutes', permissions)
        // 
        router.addRoutes(accessedRoutes)
        next({ ...to, replace: true })
      }
    }
  } else if (whiteList.includes(to.path)) {
    // next
    next()
  } else {
    // 
    next('/sign')
  }
})

router.afterEach(() => {
  NProgress.done()
})
