<template>
  <vs-popup
    title=""
    :active.sync="popupActive"
  >
    <vs-tabs
      position="left"
      v-model="active"
    >
      <vs-tab label="">
        <div class="ml-10">
          <vs-input
            class="mb-2"
            label=""
            type="password"
            v-model.trim="oldPwd"
          />
          <vs-input
            label=""
            placeholder=""
            type="password"
            v-model.trim="newPwd"
            @keyup.enter="updatePassword()"
          />
        </div>
      </vs-tab>
      <vs-tab label="">
        <div class="ml-10">
          <vs-input
            type="password"
            label=""
            placeholder=""
            v-model.trim="lockPwd"
            @keyup.enter="updateLockPassword()"
          />
        </div>
      </vs-tab>
    </vs-tabs>
  </vs-popup>
</template>

<script>
import { updatePassword, updateLockPassword } from '@/request/api/admin'

export default {
  name: 'AccountSecurity',
  props: {
    showPopup: {
      type: Boolean,
      required: true,
    },
    active: {
      type: Number,
      default: 1,
    },
  },

  data: () => ({
    popupActive: false,
    oldPwd: '',
    newPwd: '',
    lockPwd: '',
  }),

  watch: {
    showPopup: {
      handler(v) {
        this.popupActive = v
      },
      immediate: true,
    },
    popupActive(v) {
      if (!v) {
        this.$store.commit('SET_SECURITY', { show: false })
      }
    },
  },

  methods: {
    async updatePassword() {
      if (this.oldPwd.length > 0 && this.newPwd.length > 0) {
        const { code } = await updatePassword({ old_pwd: this.oldPwd, new_pwd: this.newPwd })
        if (code === 2000) {
          await this.$store.dispatch('admin/SignOut')
          this.popupActive = false
          this.$router.replace('/sign')
        }
      }
    },
    async updateLockPassword() {
      if (this.lockPwd.length > 0) {
        const { code } = await updateLockPassword({ password: this.lockPwd })
        if (code === 2000) {
          await this.$store.dispatch('admin/getAdminInfo')
          this.popupActive = false
        }
      }
    },
  },
}
</script>

<style lang="scss" scoped>
.con-vs-popup::v-deep {
  .vs-popup {
    width: 450px;
  }
}
</style>
