<template>
  <vs-popup
    class="todo-popup"
    :title="task._id ? '' : ''"
    :active.sync="isPopupActiveLocal"
  >
    <!--  -->
    <div class="flex items-center justify-between">
      <div class="todo-tag__group flex items-center">
        <template v-for="(tag, i) in task.tags">
          <div
            class="task-tag flex items-center"
            v-if="tag"
            :key="i"
          >
            <span
              class="w-2 h-2 mr-2 rounded-full"
              :class="`bg-${tags[tag].color}`"
            ></span>
            <span>{{ tags[tag].text }}</span>
          </div>
        </template>
      </div>
      <div class="flex items-center">
        <feather
          size="19"
          type="bookmark"
          class="mr-2 text-gray cursor-pointer"
          :class="{'success': task.is_important}"
          @click="$set(task, 'is_important', !task.is_important)"
        ></feather>
        <feather
          size="19"
          type="star"
          class="mr-2 text-gray cursor-pointer"
          :class="{'warning': task.is_starred}"
          @click="$set(task, 'is_starred', !task.is_starred)"
        ></feather>
        <!--  -->
        <vs-dropdown>
          <feather
            size="19"
            type="tag"
            class="mt-2 text-gray"
          ></feather>
          <vs-dropdown-menu>
            <vs-dropdown-item
              v-for="(tag, i) in tags"
              :key="i"
            >
              <vs-checkbox
                :vs-value="tag.type"
                v-model="task.tags"
                @click.stop
              >{{ tag.text }}</vs-checkbox>
            </vs-dropdown-item>
          </vs-dropdown-menu>
        </vs-dropdown>
      </div>
    </div>

    <!--  -->
    <div class="w-full pt-2">
      <!--  -->
      <vs-input
        class="w-full"
        label-placeholder=""
        v-model.trim="task.title"
      />
    </div>

    <!--  -->
    <vs-textarea
      class="mt-4"
      label=""
      v-model="task.content"
      :height="String(80)"
    />

    <!--  -->
    <div>
      <div class="mb-1 text-gray text-xs"></div>
      <el-date-picker
        type="daterange"
        value-format="timestamp"
        range-separator=""
        start-placeholder=""
        end-placeholder=""
        v-model="task.complete_time"
      >
      </el-date-picker>
    </div>

    <div class="mt-2 flex justify-end">
      <vs-button
        color="#848484"
        type="flat"
        @click="isPopupActiveLocal = false"
      ></vs-button>
      <vs-button
        id="confirmBtn"
        class="vs-con-loading__container"
        :disabled="disabled || loading"
        @click="confirm"
      >{{ task._id ? '' : '' }}</vs-button>
    </div>
  </vs-popup>
</template>

<script>
const tags = {
  1: {
    text: '',
    color: 'main',
    type: 1,
  },
  2: {
    text: '',
    color: 'warning',
    type: 2,
  },
  3: {
    text: '',
    color: 'success',
    type: 3,
  },
  4: {
    text: 'BUG',
    color: 'danger',
    type: 4,
  },
}
export default {
  name: 'TodoPopup',
  props: {
    isPopupActive: {
      type: Boolean,
      required: true,
    },
  },

  data: () => ({
    tags,
    task: {},
    loading: false,
    isPopupActiveLocal: false,
  }),

  watch: {
    isPopupActive: {
      handler(v) {
        this.isPopupActiveLocal = v
      },
      immediate: true,
    },
    isPopupActiveLocal(v) {
      if (!v) {
        // 
        this.$store.commit('todo/SET_TODO_POPUP_STATUS', { status: false })
      }
    },
    todoData(data) {
      this.task = this.$cloneDeepWith(data)
    },
  },

  computed: {
    todoData() {
      return this.$store.state.todo.todoData
    },
    disabled() {
      return !((this.task.title?.length > 0) && (this.task.complete_time?.length > 0))
    },
  },

  methods: {
    confirm() {
      if (this.task?._id) {
        this.updateTodo()
      } else {
        this.addTodo()
      }
    },

    async addTodo() {
      this.loading = true
      this.$vs.loading({
        background: 'primary',
        color: '#fff',
        container: '#confirmBtn',
        scale: 0.45,
      })
      try {
        await this.$store.dispatch('todo/addTodo', this.task)
        this.isPopupActiveLocal = false
      } finally {
        this.$vs.loading.close('#confirmBtn > .con-vs-loading')
        this.loading = false
      }
    },

    async updateTodo() {
      this.loading = true
      this.$vs.loading({
        background: 'primary',
        color: '#fff',
        container: '#confirmBtn',
        scale: 0.45,
      })
      try {
        await this.$store.dispatch('todo/updateTodo', this.task)
        this.isPopupActiveLocal = false
      } finally {
        this.$vs.loading.close('#confirmBtn > .con-vs-loading')
        this.loading = false
      }
    },
  },
}
</script>

<style lang="scss" scoped>
// 
.todo-popup::v-deep {
  .vs-popup {
    width: 450px;
  }
}

//  mark 
.todo-tag__group {
  height: 30px;
  .task-tag {
    margin: 0 5px;
    padding: 3px 10px;
    border-radius: 10px;
    font-size: 14px;
    background-color: #e6e6e6;
  }
}
</style>

