<template>
  <transition
    enter-active-class="animated fadeIn"
    leave-active-class="animated fadeOut"
    mode="out-in"
  >
    <div
      class="nav-wrapper"
      v-show="navbarType !== 'hidden'"
      :class="[{ collapse: sidebarCollapse }, navbarType]"
    >
      <div class="nav-bar rounded-lg">
        <div class="flex items-center">
          <router-link
            v-for="(item, index) in navIcons"
            :key="index"
            :to="item.route"
          >
            <el-tooltip
              :content="item.tip"
              effect="light"
            >
              <feather
                class="nav-icon mr-4"
                :type="item.icon"
              ></feather>
            </el-tooltip>
          </router-link>
        </div>
        <div class="nav-right">
          <div class="flex items-center">

            <!--  -->
            <el-tooltip
              :open-delay="100"
              :content="showSearchInput ? '' : ''"
              effect="light"
            >
              <feather
                class="nav-icon"
                :type="showSearchInput ? 'x' : 'search'"
                @click="showSearchInput = !showSearchInput"
              ></feather>
            </el-tooltip>
            <vs-input
              class="nav-search overflow-hidden"
              :class="showSearchInput ? 'w-48': 'w-0'"
              placeholder=""
              @keyup.esc="showSearchInput = false"
              @keyup.enter="search"
              v-model="searchText"
            />

            <!--  -->
            <el-tooltip
              :open-delay="100"
              :content="isFullScreen ? '' : ''"
              effect="light"
            >
              <feather
                class="nav-icon mx-4"
                :type="isFullScreen ? 'minimize' : 'maximize'"
                @click="screenfull"
              ></feather>
            </el-tooltip>

            <!--  -->
            <Notice />

            <!--  -->
            <Avatar class="ml-5" />
          </div>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
import screenfull from 'screenfull'
import { mapState } from 'vuex'

import Notice from './the-navbar/Notice.vue'
import Avatar from './the-navbar/Avatar.vue'

const navIcons = [
  { tip: '', icon: 'package', route: '/goods-list' },
  { tip: '', icon: 'clipboard', route: '/order-list' },
  { tip: '', icon: 'users', route: '/user-list' },
  { tip: '', icon: 'shopping-bag', route: '/buying-list' },
]

export default {
  name: 'TheNavBar',
  components: { Notice, Avatar },

  data: () => ({
    navIcons,
    searchText: '',
    showSearchInput: false,
    isFullScreen: false, // ,
  }),

  computed: {
    ...mapState(['sidebarCollapse', 'navbarType']),
  },

  mounted() {
    if (screenfull.enabled) {
      screenfull.on('change', this.screenChange)
    }
  },

  beforeDestroy() {
    if (screenfull.enabled) {
      screenfull.off('change', this.screenChange)
    }
  },

  methods: {
    // 
    screenfull() {
      if (!screenfull.enabled) {
        this.$vs.notify({
          time: 3000,
          title: '',
          text: '',
          color: 'warning',
        })
        return
      }
      screenfull.toggle()
    },
    screenChange() {
      this.isFullScreen = screenfull.isFullscreen
    },

    // 
    search() {
      console.log(this.searchText)
    },
  },
}
</script>

