import Axios from 'axios'
import { Notification } from 'element-ui'
import { getToken } from '@/permission/token'

const service = Axios.create({
  // baseURL: '/api',
  baseURL: process.env.NODE_ENV === 'production' ? 'http://129.204.137.172/api' : '/api',
  // baseURL: 'http://127.0.0.1:7001/api',
  timeout: 10000,
  responseType: 'json',
  withCredentials: true,
})

service.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded;charset=utf-8'

service.interceptors.request.use(
  (config) => {
    // token
    const token = getToken()
    if (token) {
      config.headers.Authorization = `Bearer ${token}`
    }

    return config
  },
  (error) => {
    Promise.reject(error)
  },
)

const errorHandler = {
  errorNotify({
    title = '', message = '', duration = 3500,
  } = {}) {
    Notification.error({
      title,
      message,
      duration,
    })
  },
  401(status, statusText) {
    this.errorNotify({ title: `${status}`, message: ` - ${statusText}` })
  },
  404(status, statusText) {
    this.errorNotify({ title: `${status}`, message: ` - ${statusText}` })
  },
  418(status) {
    this.errorNotify({ title: `${status}`, message: '~', duration: 3000 })
  },
  500(status, statusText) {
    this.errorNotify({ title: `${status}`, message: ` - ${statusText}` })
  },
  default() {
    this.errorNotify()
  },
}

service.interceptors.response.use(
  (response) => {
    const { data } = response
    const { code, msg } = data
    if (code >= 4000) {
      errorHandler.errorNotify({
        title: ` - ${code}`,
        message: msg,
        duration: 5000,
      })
    }
    return data
  },
  (error) => {
    console.log(error, error.response)
    const { status = 'default', statusText } = error.response
    /* eslint no-unused-expressions: [2, { allowTernary: true }] */
    Object.prototype.hasOwnProperty.call(errorHandler, status)
      ? errorHandler[status](status, statusText)
      : errorHandler.default()
    return Promise.reject(error)
  },
)

export default service
