import { setToken, getToken, removeToken } from '@/permission/token'
import { signIn, getAdminInfo } from '@/request/api/admin'
import { resetRouter } from '@/router/router'

const state = {
  token: getToken(),
  info: {},
}

const mutations = {
  SET_TOKEN(state, token) {
    state.token = token
  },
  SET_INFO(state, info) {
    state.info = info
  },
  SET_AVATAR(state, url) {
    state.info.avatar_url = url
  },
}

const actions = {
  async signIn({ commit }, payload) {
    try {
      const { code, data } = await signIn(payload)
      if (code === 2000) {
        commit('SET_TOKEN', data.token)
        setToken(data.token) //  token  cookie
      }
      return code
    } catch (err) {
      return err
    }
  },

  async SignOut({ commit }) {
    localStorage.removeItem('screen_lock')
    localStorage.removeItem('vuex')
    commit('SET_TOKEN', '')
    removeToken() // token
    resetRouter() // 
  },

  async getAdminInfo({ commit }) {
    try {
      const { code, data: { admin_info } } = await getAdminInfo()
      if (code === 2000) {
        commit('SET_INFO', admin_info)
      }
      return admin_info
    } catch (err) {
      removeToken()
      return Promise.reject(err)
    }
  },
}

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters: {
    adminId: state => state.info._id,
    userId: state => state.info?.user?._id,
    nickname: state => state.info.nickname,
    avatar: state => state.info.avatar_url,
    lockPwd: state => state.info?.lock_password,
  },
}
